import { useEffect, useState } from "react"
import updateTimer from "./timer"
import './homepage.css'

const Homepage = () => {
    const [arr, setarr] = useState([])

    useEffect(() => {
        const timer = setInterval(() => {
            setarr([...updateTimer()])
        }, 1000)
        return () => clearInterval(timer)
    }, [])

    return (
        <div className='main'>
            <div className="header">{arr[4] ? <p>Christmas is today!</p> : <p>Christmas is in...</p>}</div>
            {arr[4] ? <h1 className="big">Merry Christmas Everyone!</h1>:
            <div className="container">
                <div className="containers">
                    <p className="big">{arr[0]}</p>
                    <span>days</span>
                </div><div className="containers">
                        <p className="big">{arr[1]}</p>
                        <span>hours</span>
                    </div><div className="containers">
                        <p className="big">{arr[2]}</p>
                        <span>minutes</span>
                    </div><div className="containers">
                        <p className="big">{arr[3]}</p>
                        <span>seconds</span>
                    </div>
            </div>}
        </div>
    )
}

export default Homepage