
function formater(a){
    if(a<10){
        return `0${a}`
    }else{
        return a
    }
}

export default function updateTimer(){
    const now = new Date()
    let goal = new Date(`12/22/${now.getFullYear()}`)
    let today = false

    let totalSeconds = (goal-now)/1000
    let secondsInADay = 60*60*24
    let days = Math.floor(totalSeconds/60/60/24)
    let hours = Math.floor(totalSeconds/3600)%24
    let minutes = Math.floor(totalSeconds/60)%60
    let seconds = Math.floor(totalSeconds)%60

    if (totalSeconds<0 && totalSeconds>secondsInADay){
        today = true
    }else if(totalSeconds-secondsInADay < 0){
        const goal = new Date(`12/20/${now.getFullYear()+1}`)
    
        totalSeconds = (goal-now)/1000
        secondsInADay = 60*60*24
        days = Math.floor(totalSeconds/60/60/24)
        hours = Math.floor(totalSeconds/3600)%24
        minutes = Math.floor(totalSeconds/60)%60
        seconds = Math.floor(totalSeconds)%60
    }

    return [days,formater(hours),formater(minutes),formater(seconds),today]
}